* {
  box-sizing: border-box;
}

body {
  font-family: $font-open-sans;
}

a {
  text-decoration: none;
}

h2 {
  font-family: $font-raleway;
  font-size: 2.25rem;
}
.container {
  width: 1352px;
  margin: 0 auto;
}
@media (max-width:1352px){
  .container{
    width:100%;
    padding:0 15px;
  }
}