.benefits {
  padding: 100px 0;
  background-color: #F7F7F7;
}

.benefits__container {
  padding: 0 100px;
}

.benefits__single {
  background-color: #ffffff;
  border: 1px solid #E8E9ED;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 37px;
  border-radius: 5px;
}

.benefits__text1, .benefits__text2, .benefits__text3 {
  box-sizing: content-box;
  max-width: 415px;
  margin: 90px 36px;
}

.benefits__text1::after {
  content: "";
  width: 332px;
  height: 4px;
  background-color: #FB3B64;
  display: block;
  margin-top: 50px;
}

.benefits__text2::after {
  content: "";
  width: 332px;
  height: 4px;
  background-color: #08A6E4;
  display: block;
  margin-top: 50px;
}

.benefits__text3::after {
  content: "";
  width: 332px;
  height: 4px;
  background-color: #55DFB4;
  display: block;
  margin-top: 50px;
}

.benefits__description {
  margin-top: 30px;
  line-height: 1.375rem;
}

.benefits__image {
  position: relative;
}

.benefits__image img {
  position: relative;
  z-index: 2;
}

.benefits__single--notebook .benefits__image {
  margin-right: 117px;
  margin-bottom: 38px;
}

.benefits__single--notebook .benefits__image::before {
  content: "";
  width: 180px;
  height: 180px;
  background-color: #FB3B64;
  display: block;
  border-radius: 40px;
  position: absolute;
  right: 0;
  top: -40px;
  transform: rotate(45deg);
}

.benefits__single--notebook .benefits__image img {
  width: 386px;
  height: auto;
  position: relative;
  z-index: 2;
}

.benefits__single--phone {
  flex-direction: row-reverse;
  padding: 0 20px 0 70px;
}

.benefits__single--phone .benefits__image::before {
  content: "";
  width: 320px;
  height: 320px;
  background-color: #08A6E4;
  display: block;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: -20px;
}

.benefits__single--phone .benefits__image img {
  width: 426px;
  height: auto;
}

.benefits__single--phone .benefits__text {
  margin: 70px 0;
}

.benefits__double {
  display: flex;
}

.benefits__double > div {
  margin-bottom: 0;
}

.benefits__single--solo {
  max-width: 620px;
}

.benefits__single--solo .benefits__text {
  max-width: none;
  margin: 70px 35px;
}

.benefits__single--image {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #55DFB4;
  border: none;
  margin-left: 60px;
}

.benefits__single--image img {
  width: 295px;
  height: auto;
  margin-top: 55px;
}

@media (max-width: 1000px) {
  .benefits {
    padding-top: 40px;
  }

  .benefits__container {
    padding: 0 30px;
  }

  .benefits__single {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  /* First */
  .benefits__image {
    order: 1;
    width: 70%;
  }

  .benefits__single--notebook .benefits__image {
    margin: 37px 55px 27px 55px;
  }

  .benefits__single--notebook .benefits__image::before {
    width: 20vw;
    height: 20vw;
    right: 0;
    top: -20px;
  }

  .benefits__text1 {
    order: 2;
    margin: 0 21px 30px 21px;
  }

  .benefits__text1::after {
    display: none;
  }

  .benefits__text1 .benefits__description {
    margin-top: 82px;
  }

  .benefits__single--notebook .benefits__image img {
    width: 100%;
    height: auto;
  }

  /* First */
  /* Second */
  .benefits__text2 {
    order: 2;
    margin: 0 21px 30px 21px;
  }

  .benefits__text2::after {
    display: none;
  }

  .benefits__single--phone .benefits__image {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }

  .benefits__single--phone .benefits__image img {
    width: 80%;
    height: auto;
  }

  .benefits__single--phone .benefits__image::before {
    width: 35vw;
    height: 35vw;
    left: 35px;
    top: -10px;
  }

  .benefits__single--phone {
    padding: 0;
  }

  /* Second */
  /* Third */
  .benefits__double {
    flex-direction: column;
    box-sizing: border-box;
  }

  .benefits__single--image {
    width: 100%;
    margin: 0;
  }

  .benefits__single--solo {
    max-width: 100%;
  }

  .benefits__single--image img {
    width: 100%;
    height: auto;
    margin-top: 20px;
  }

  .benefits__double {
    width: 100%;
  }

  .benefits__text3 {
    margin: 20px 21px 30px 21px;
  }

  .benefits__text3::after {
    display: none;
  }
}
.contact {
  padding: 80px 100px 100px;
  display: flex;
  justify-content: center;
}

.contact__column {
  width: 50%;
}

.contact__title {
  font-size: 3.0625rem;
  line-height: 3.6875rem;
}

.contact__instruction {
  margin: 22px 0 42px;
  font-size: 1rem;
  line-height: 1.375rem;
}

.contact__details {
  display: flex;
  justify-content: flex-start;
  color: #A5A5A5;
  align-items: center;
}

.details__item {
  color: #A5A5A5;
  font-weight: 500;
  text-decoration: none;
}

.details__item:first-of-type {
  margin-right: 20px;
}

.details__item img {
  margin-right: 10px;
}

/**
 * Form
 */
.form {
  display: flex;
  flex-direction: column;
}

.form__field {
  display: flex;
  flex-direction: column;
}

.form__field:first-of-type {
  margin-bottom: 42px;
}

.form__label {
  font-weight: bold;
  text-transform: uppercase;
  color: #08A6E4;
  line-height: 0.9rem;
  letter-spacing: 3px;
  font-size: 0.6875rem;
}

.form__input {
  border: none;
  border-bottom: 1px solid #A5A5A5;
  background-color: transparent;
  line-height: 2.625rem;
}

.form__consent {
  margin-top: 30px;
}

.form__checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 1.375rem;
}

.form__checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.form__checkbox .checkbox {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #ffffff;
  border: 1px solid #A5A5A5;
  border-radius: 4px;
}

.form__checkbox input:checked ~ .checkbox {
  background-image: url(../assets/Checkmark.svg);
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
}

.form__checkbox .consent__text {
  margin: 0 10px;
  font-size: 0.75rem;
  color: #A5A5A5;
  line-height: 17px;
}

.form .btn {
  font-size: 0.8125rem;
  padding: 20px 97px;
  align-self: flex-end;
  margin-top: 20px;
}

@media (max-width: 900px) {
  .contact {
    flex-direction: column;
    align-items: flex-start;
    padding: 0 0 0 15px;
    width: 90%;
  }

  .contact__title {
    font-size: 2.3rem;
    line-height: 2.3rem;
  }

  .contact__details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .details__item {
    padding: 5px 0 5px 0;
  }

  .contact__column {
    width: 100%;
  }

  .form {
    width: 100%;
    margin-bottom: 44px;
  }
}
