/**
 * Opening section
 */
.opening {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.opening__content {
  margin-top: 130px;
  text-align: center;
}

.opening__header {
  font-family: "Raleway", sans-serif;
  font-size: 3.0625rem;
}

.opening__heading {
  margin-top: 20px;
  margin-bottom: 38px;
}

.opening__image {
  width: 100%;
  height: auto;
}

.ham {
  display: none;
}

@media (max-width: 1000px) {
  /* Hamburger */
  /* List */
  .header__nav {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
    height: auto;
    background-color: white;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    position: absolute;
    top: 70px;
    left: 0;
    visibility: hidden;
  }

  .nav-head-active .header__nav {
    visibility: unset;
  }

  .nav__link {
    display: block;
    padding: 10px 0;
  }

  .nav__link:hover {
    color: #000000;
    border: none;
  }

  .nav__element:first-child {
    padding-top: 20px;
  }

  .nav__element:last-child {
    padding-bottom: 20px;
  }

  /* List */
  .ham {
    padding: 10px;
    cursor: pointer;
    border: 0;
    margin: 0;
    background: transparent;
    display: inline-block;
  }

  .ham-box {
    width: 34px;
    height: 24px;
    display: inline-block;
    position: relative;
  }

  .ham-inner {
    width: 100%;
    height: 3px;
    position: absolute;
    background-color: #08A6E4;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }

  .ham-inner::before, .ham-inner::after {
    width: 100%;
    height: 3px;
    position: absolute;
    background-color: #08A6E4;
    content: "";
    left: 0;
  }

  .ham-inner::before {
    top: -10px;
  }

  .ham-inner::after {
    top: 10px;
  }

  .ham-active .ham-inner {
    background-color: transparent;
    transition: background-color 0.2s;
  }

  .ham-active .ham-inner::before {
    transform: translateY(10px) rotate(45deg);
  }

  .ham-active .ham-inner::after {
    transform: translateY(-10px) rotate(-45deg);
  }

  /* Hambuer */
  .btn--large {
    padding: 22px 100px;
    margin: 21px 0 51px 0;
  }

  .opening {
    justify-content: center;
    align-items: center;
  }

  .opening__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .opening__heading {
    display: inline-block;
    margin: 20px 0 7px 0;
    width: 75%;
  }

  .opening__image {
    height: 45vw;
  }
}
