}

.pricing__price1 {
  color: #FB3B64;
}

.pricing__price2 {
  color: #08A6E4;
}

.pricing__price3 {
  color: #55DFB4;
}

.pricing__limited {
  text-align: center;
  margin-top: 15px;
  font-size: 0.75rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

.pricing__items {
  text-align: center;
}

.pricing__item {
  font-size: 14px;
  margin-bottom: 20px;
}

.pricing__item::before {
  content: "";
  width: 11px;
  height: 11px;
  display: inline-block;
  margin-right: 10px;
  background-image: url("../assets/Checkmark-1.svg");
}

.pricing__item--not::before {
  background-image: url("../assets/Close.svg");
}

.pricing__box1 .btn, .pricing__box2 .btn, .pricing__box3 .btn {
  position: relative;
  bottom: -25px;
  font-size: 0.8125rem;
}

.pricing__box1 .btn {
  background-color: #FB3B64;
}

.pricing__box2 .btn {
  background-color: #08A6E4;
}

.pricing__box3 .btn {
  background-color: #55DFB4;
}

