.pricing__box--basic .pricing__price {
  margin-bottom: 30px;
}

/*** Professional Box **/
.pricing__box--professional {
  width: 400px;
  margin: 0 40px;
  box-shadow: 5px 1px 27px rgba(7, 172, 230, 0.16);
}

.pricing__box--professional .pricing__type {
  font-size: 1.3125rem;
}

.pricing__box--professional .pricing__item {
  margin-bottom: 25px;
}

@media (max-width: 1000px) {
  .pricing {
    margin-top: 54px;
  }

  .pricing__boxes {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 14px;
  }

  .pricing__box1, .pricing__box2, .pricing__box3 {
    margin-bottom: 54px;
  }

  .details-cont {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    height: 100%;
    margin-top: 40px;
  }

  .pricing__details {
    margin: 0 20px 0 20px;
  }

  .pricing__items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  .pricing__box1, .pricing__box3 {
    width: 70vw;
    max-width: 400px;
    height: 35vh;
  }

  .pricing__box3 {
    padding-top: 10px;
  }

  .pricing__box2 {
    width: 80vw;
    max-width: calc(400px + 10vw);
