$color-primary: #08A6E4;
$color-primary-shadow: rgba(7, 172, 230, .16);
$color-redish: #FB3B64;
$color-green: #55DFB4;
$color-black: #000000;
$color-white: #ffffff;
$color-grey: #A5A5A5;
$color-light-grey: #F7F7F7;
$button-radius: 35px;
$font-raleway: "Raleway", sans-serif;
$font-open-sans: "Open Sans", sans-serif;
@mixin hamburger-line{
  width:100%;
  height:3px;
  position:absolute;
  background-color:$color-primary;
}
